import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import yuangong from '@/views/modules/yuangong/list'
    import caigouxuqiu from '@/views/modules/caigouxuqiu/list'
    import wuzifenlei from '@/views/modules/wuzifenlei/list'
    import wuzibaosun from '@/views/modules/wuzibaosun/list'
    import wuziruku from '@/views/modules/wuziruku/list'
    import wuzikucun from '@/views/modules/wuzikucun/list'
    import wuzichuku from '@/views/modules/wuzichuku/list'
    import wuzidiaobo from '@/views/modules/wuzidiaobo/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/yuangong',
        name: '',
        component: yuangong
      }
      ,{
	path: '/caigouxuqiu',
        name: '',
        component: caigouxuqiu
      }
      ,{
	path: '/wuzifenlei',
        name: '',
        component: wuzifenlei
      }
      ,{
	path: '/wuzibaosun',
        name: '',
        component: wuzibaosun
      }
      ,{
	path: '/wuziruku',
        name: '',
        component: wuziruku
      }
      ,{
	path: '/wuzikucun',
        name: '',
        component: wuzikucun
      }
      ,{
	path: '/wuzichuku',
        name: '',
        component: wuzichuku
      }
      ,{
	path: '/wuzidiaobo',
        name: '',
        component: wuzidiaobo
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
